
































































































































































































































































import { Component, Vue } from "vue-property-decorator";
import Player from "xgplayer";
import ImageDropLets from "@/components/home/ImageDropLets.vue";
@Component({
    components: {
        ImageDropLets
    }
})
export default class Video extends Vue {
    private tabActive?: number = 0;
    private tabItems?: any[] | any = [
        { text: "黄驴推荐", name: "DonRecommended" },
        { text: "高清无码", name: "Uncoded" }
    ];
    private images?: any[] = ["img1", "img2", "img3", "img4", "img5", "img6"];
    private changeTab(index: number) {
        this.tabActive = index;
    }
    private showTop: boolean = true;
    private showPopup: boolean = true;
    private themeColor: string = "";

    private like: boolean = false;
    private favor: boolean = false;
    private dislike: boolean = false;
    private down: boolean = false;

    private back(){
        this.$router.back()
    }
    private changeLike() {
        this.like = !this.like;
        if (this.like) {
            this.dislike = false;
        }
    }
    private changeDislike() {
        this.dislike = !this.dislike;
        if (this.dislike) {
            this.like = false;
        }
    }
    private changeFavor() {
        this.favor = !this.favor;
    }
    private changeDown() {
        this.down = !this.down;
    }
    private gtouchstart(){
        this.showTop = !this.showTop
    }
    mounted() {
        this.themeColor = this.$store.state.themeColor;
        let player = new Player({
            id: "mse",
            autoplay: false,
            volume: 0.3,
            url:
                "//sf1-hscdn-tos.pstatp.com/obj/media-fe/xgplayer_doc_video/mp4/xgplayer-demo-720p.mp4",
            poster:
                "//s2.pstatp.com/cdn/expire-1-M/byted-player-videos/1.0.0/poster.jpg",
            playsinline: true,
            height: 203,
            width: window.innerWidth
        });
    }
}
